
.main-content {
  padding: 24px 16px;
}


.post-list {
  display: grid;
  gap: 14px;
  max-width: 1000px;
  margin: 0 auto;
}


.panel {
  background: #d9f0ff;           
  border: 2px solid #2a68ff;     
  border-radius: 14px;
  padding: 12px;
}


.post-card {
  display: grid;
  grid-template-columns: 220px 1fr;
  align-items: center;
  gap: 14px;
}

@media (max-width: 720px) {
  .post-card {
    grid-template-columns: 1fr;
  }
}


.post-img {
  width: 100%;
  height: 120px;
  object-fit: cover;
  border-radius: 10px;
  display: block;
}


.post-content {
  text-align: left;
}


.post-heading {
  margin: 0 0 6px;
  font-size: 18px;
  font-weight: 800;
}

.post-excerpt {
  margin: 0 0 6px;
  color: #0b315b;
}


.read-more a {
  color: #0b63d8;
  font-weight: 700;
  text-decoration: underline;
}


.post-by,
.post-author {
  color: #d04b2f; 
  font-weight: 700;
}


.post-detail {
  max-width: 1000px;
  margin: 0 auto;
}

.post-detail-row {
  display: grid;
  grid-template-columns: 260px 1fr;
  gap: 16px;
  align-items: start;
}

@media (max-width: 820px) {
  .post-detail-row {
    grid-template-columns: 1fr;
  }
}

.post-detail-img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 10px;
}

.post-title {
  margin: 6px 0 10px;
  font-weight: 800;
}


.back-link a,
.back-link {
  color: #0b63d8;
  font-weight: 700;
  text-decoration: underline;
  margin-top: 8px;
}
